import React from "react";
import PropTypes from "prop-types";

function SkipToContent({ content }) {
  return (
    <>
      <a
        className="skip"
        href={"#" + content}
        aria-label="Skip to main Content"
      >
        Skip to Content
      </a>
    </>
  );
}

export default SkipToContent;

SkipToContent.propTypes = {
  content: PropTypes.string,
};
